.App {
    font-family: sans-serif;
    text-align: center;
  }
  .grid-container {
    display: grid;
    grid-template-columns: auto auto auto auto auto auto auto auto auto auto;
    grid-template-rows: auto auto 200px ;
    grid-gap: 15px;
    background-color: #fdfdfd;
    padding: 5px;
  }
  .grid-item-logo{
    grid-column:3 / span 1;
    grid-row: 1;
  }
  .grid-item-box{
    grid-column:3 / span 6;
    grid-row: 2;
    background-color:#0A0224;
    height:5em;
    border-radius: 7px;
  }
  .item-state-text{
    text-align: center;
    padding: 25px;
    font-size: 15px;
    color: rgb(255, 255, 255);
  }
/* Form 1 */
  form{
    display: grid;
    grid-column:3 / span 6;
    grid-row: 3 / span 6;
    grid-gap: 10px;
    grid-template-columns: auto auto auto auto auto auto;
    grid-template-rows: auto auto auto auto auto auto auto auto auto;
    border-color: aqua;
    /* background-color:greenyellow; */
    height: 10em;
    
  }
  .item-btn-f1{
    color:rgb(255, 255, 255);
    background-color: rgb(25, 0, 255,0.8);
    border-radius: 1ex;
    border-color: rgb(25, 0, 255, 0.7);
    grid-column:3 / span 2;
    grid-row: 4 / span 1;
    font-size: 10px;
  }
  .item-btn-f1:hover{
    background-color: rgb(0, 0, 255,1);
  }
  .boxInput{    
    font-size: 7px;
    grid-column:3 / span 1 ;
    grid-row: 1 / span 1;

  }
  #ibox2{
    grid-column:4 / span 1;
    grid-row: 1 / span 1;
  }
  #ibox3{
    grid-column:3 / span 2;
    grid-row: 2 / span 1;
  }
  #ibox4{
    grid-column:3 / span 2;
    grid-row: 3 / span 1;
  }
  .inputSize{
    width: 100%;
    border:none;
    border-bottom: 1px solid rgba(0,0,0, 0.4);
  }
  .textalign-left{
    text-align:left;
  }
  .textalign-right{
    text-align:right;
  }
.color1{
  color:rgba(185, 185, 181, 0.932);
}
.color2{
  color:black;
}
/* ####################################### */
/* Form 2 */
.editButton{
  font-size: 9px;
  background-color: white;
  color: rgb(0, 0, 255);
  border:none;
}
.editButton:hover{
  color: rgb(0, 0, 255, 0.5);
}
.editImage{
  width:16px;
  height:16px;
  padding:4px;
}
.items-journey{
  font-size: 7px;
  grid-column:3 / span 3 ;
  grid-row: 1 / span 1;
  border:none;
  width:80%;
  border-bottom: 1px solid rgba(0,0,0, 0.4);
}
#amount{
  font-size: 25px;
  grid-column:3 / span 3 ;
  grid-row: 2 / span 1;
  border-bottom:none;
}
.amount-input{
  width:20%;
  height:100%;
  border: none;
}
#btn-f2{
  grid-column:3 / span 3 ;
  grid-row: 6 / span 1;
  width:80%;
}
#ibox5{
  grid-column:3 / span 3 ;
  grid-row: 3 / span 1;
  width:80%;
}
#ibox6{
  grid-column:3 / span 3 ;
  grid-row: 4 / span 1;
  width:80%;
}
#ibox7{
  grid-column:3 / span 3 ;
  grid-row: 5 / span 1;
  width:80%;
}